  	<div class="content">
  		<blockquote>
  <p></p><p><strong><span style="color: rgb(255, 204, 0); font-family: 宋体; font-size: 19px;">产品介绍</span></strong></p><p style="text-indent: 28px;"><span style="font-family:;" times="" new="">AT-65</span><span style="font-family: 宋体;">全自动COD滴定仪包括双高精度注射泵加液系统，搅拌系统，</span><span style="font-family:;" times="" new="">XYZ</span><span style="font-family: 宋体;">三轴移动系统，清洗系统，复合电极以及</span><span style="font-family:;" times="" new="">PC</span><span style="font-family: 宋体;">控制系统。采用电位滴定原理，样品通量</span><span style="font-family:;" times="" new="">65</span><span style="font-family: 宋体;">位，最大可支持</span><span style="font-family:;" times="" new="">90</span><span style="font-family: 宋体;">位。可用于替代人工滴定实现，相比传统电位滴定，具有样品通量大，终点判断准确，操作软件设计灵活，具备报告输出等功能，安全环保，使用方便，经济实用等特点。</span></p><p style="text-align: center;"><br></p><p><strong><span style="color: rgb(255, 204, 0); font-family: 宋体; font-size: 19px;">产品特点</span></strong></p><p><span style="font-family:;" times="" new="">1、<span times="" new="">&nbsp; </span></span><span style="font-family: 宋体;">双高精度</span><span style="font-family:;" times="" new="">25mL</span><span style="font-family: 宋体;">注射泵，分辨率达</span><span style="font-family:;" times="" new="">1/48000</span><span style="font-family: 宋体;">步，加液准确。</span></p><p><span style="font-family:;" times="" new="">2、<span times="" new="">&nbsp; </span></span><span style="font-family:;" times="" new="">XYZ</span><span style="font-family: 宋体;">三轴移动系统，走位精确，重复精度达</span><span style="font-family:;" times="" new="">0.5mm</span><span style="font-family: 宋体;">。</span></p><p><span style="font-family:;" times="" new="">3</span><span style="font-family: 宋体;">、进口电极，灵敏度更高，更稳定</span></p><p><span style="font-family:;" times="" new="">4</span><span style="font-family: 宋体;">、高速搅拌器，最高搅拌速度可达</span><span style="font-family:;" times="" new="">10000</span><span style="font-family: 宋体;">转</span><span style="font-family:;" times="" new="">/</span><span style="font-family: 宋体;">分钟，瞬时混合均匀</span></p><p><span style="font-family:;" times="" new="">5</span><span style="font-family: 宋体;">、操作软件采用可视化设计，操作方便，具备报告输出功能，符合</span><span style="font-family:;" times="" new="">GMP</span><span style="font-family: 宋体;">，测定数据数据库形式保存，具备追溯功能。</span></p><p><span style="font-family:;" times="" new="">6</span><span style="font-family: 宋体;">、适用项目：废水中</span><span style="font-family:;" times="" new="">COD</span><span style="font-family: 宋体;">的测定，土壤中有机质的测定、食品中酸价、过氧化值测定等</span></p><p><span style="font-family: 宋体;">7、适用标准：HJ 828-2017 水质 化学需氧量 重铬酸盐法</span></p><p><span style="font-family: 宋体;"></span>&nbsp;</p><p><strong><span style="color: rgb(255, 204, 0); font-family: 宋体; font-size: 19px;">仪器参数</span></strong></p><p><span style="font-family: 宋体;">1</span><span style="font-family: 宋体;">、220V电压，10A电流，频率50-60Hz</span></p><p><span style="font-family: 宋体;">2</span><span style="font-family: 宋体;">、铂金复合电极，-1999mA-1999mA</span></p><p><span style="font-family: 宋体;">3</span><span style="font-family: 宋体;">、高精度注射泵，48000步精准控制</span></p><p><span style="font-family: 宋体;">4</span><span style="font-family: 宋体;">、5个样品架，其中4个可移动</span></p><p><span style="font-family: 宋体;">5</span><span style="font-family: 宋体;">、投入式搅拌器，搅拌速度可设定</span></p><p><span style="font-family: 宋体;">6</span><span style="font-family: 宋体;">、双通道清洗泵，清洗速度可设定</span><span style="font-family: 宋体;">，清洗干净，可有效避免交叉污染</span></p><p><span style="font-family: 宋体;">7、</span><span style="font-family: 宋体;">数据可打印输出，或按照模板输出电子报告</span></p><p><span style="font-family: 宋体;">8</span><span style="font-family: 宋体;">、</span><span style="font-family: 宋体;">仪器尺寸L850mm*W630mm*H580mm，仪器质量65kg</span></p><p><span style="font-family: 宋体;">9</span><span style="font-family: 宋体;">、环境条件-10-35℃，相对湿度25-85%</span></p><p><span style="font-family: 宋体;"><span style="text-align: left; color: rgb(255, 204, 0); text-transform: none; text-indent: 0px; letter-spacing: normal; font-family: 宋体; font-size: 19px; font-style: normal; font-variant: normal; font-weight: 700; text-decoration: none; word-spacing: 0px; float: none; display: inline !important; orphans: 2; background-color: transparent; -webkit-text-stroke-width: 0px;">实验数据</span></span></p><p><span style="color: rgb(0, 80, 0); font-family: 宋体; font-size: 24px;"><span style="font-size: 18px;">*实验室选用盲样分别采用经典回流法和快速密闭法进行消解，最后对人工与全自动COD消解仪滴定结果做比对，比对结果如下：</span></span></p><p><span style="color: rgb(0, 80, 0);"></span></p><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p><table cellspacing="0" cellpadding="0"><colgroup><col width="110" style="width: 111px;"><col width="99" style="width: 99px;"><col width="109" style="width: 109px;"><col width="109" style="width: 109px;"><col width="92" style="width: 92px;"><col width="107" style="width: 107px;"></colgroup><tbody><tr height="43" class="firstRow" style="height: 43px;"><td width="320" height="43" style="border-width: 2px; border-style: solid; border-color: windowtext black windowtext windowtext; text-align: center; background-color: transparent;" colspan="3"><strong><span style="font-size: 24px;">经典回流法</span></strong></td><td width="309" style="border-width: 2px 2px 2px 0px; border-style: solid solid solid none; border-color: windowtext black; text-align: center; -ms-word-break: break-all; background-color: transparent;" colspan="3"><strong><span style="font-size: 24px;">快速密闭法</span></strong></td></tr><tr height="32" style="height: 32px;"><td width="122" height="32" style="border-width: 0px 1px 2px 2px; border-style: none solid solid; border-color: black windowtext windowtext; text-align: center; background-color: transparent;"><span style="font-family: 宋体,SimSun;">QC</span></td><td width="68" style="border-width: 0px 1px 2px 0px; border-style: none solid solid none; border-color: black windowtext windowtext black; text-align: center; background-color: transparent;"><span style="font-family: 宋体,SimSun;">人工滴定</span></td><td style="border-width: 0px 2px 2px 0px; border-style: none solid solid none; border-color: black windowtext windowtext black; text-align: center; background-color: transpare